.burger-menu {
    // visibility: hidden;
    height: 100vh;
    padding: 23px;
  
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background-color: #000;
  
    transform: translateX(100%);
  
    transition: transform 0.3s;
  
    &--open {
      // visibility: visible;
      transform: translateX(0);
    }
  
    &__header__container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 80px;
      width: 100%;
      padding: 5px 0;
    }
  
    &__footer__container {
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 30px;
      margin-bottom: 30px;
    }
  
    &__logo {
      font-family: 'Inter', sans-serif;
      font-weight: 800;
    }
  
    &__iconLink {
      width: 24px;
      height: 24px;
      background-image: url(../../../svgs/menuClose.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  
    &__circe__link {
      display: flex;
      align-items: center;
      justify-content: center;
  
      width: 31px;
      height: 31px;
      border: 1px solid #fff;
      border-radius: 50%;
    }
  
    &__profile__inner {
      width: 15px;
      height: 19px;
      background-image: url(../../../svgs/Profile.svg);
    }
  
    &__cart__inner {
      width: 16px;
      height: 16px;
      background-image: url(../../../svgs/Cart.svg);
    }
  }
  
  .list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  
    gap: 30px;
  
    list-style-type: none;
  
    margin: 0 0 60px 3px;
  
    &__link {
      font-weight: 400;
      text-decoration: none;
      font-family: 'Inter', sans-serif;
  
      transition: font-weight 0.3s;
  
      &:active {
        font-weight: 700;
      }
    }
  }